.container {
  max-width: 100%;
  padding: 1rem;
  font-family: Arial, sans-serif;
}

.title {
  text-align: center;
  margin-bottom: 1rem;
}

.input {
  width: 100%;
  padding: 0.5rem;
  margin-bottom: 0.5rem;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.button {
  width: 100%;
  padding: 0.5rem;
  background-color: #6b778d;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-bottom: 1rem;
}

.calculate-button {
  background-color: #263859 ;
}

.table-container {
  overflow-x: auto;
}

.table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 1rem;
}

.cell, .header-cell {
  border: 1px solid #ddd;
  padding: 0.5rem;
  text-align: left;
}

.header-cell {
  background-color: #f2f2f2;
  font-weight: bold;
}

.center {
  text-align: center;
}

.result-title {
  font-weight: bold;
  margin-bottom: 0.5rem;
}

@media (min-width: 768px) {
  .container {
    max-width: 768px;
    margin: 0 auto;
  }
}

input[type="checkbox"] {
  transform: scale(1.5);
}